/* eslint-disable class-methods-use-this */
import {
  Day, AppError, ErrorCode, ListDayType, ToDoList,
} from '@/types/shared';
import HabitService from './habitService';
import ToDoService from './todoService';
import { STORAGE_KEYS, ListObj } from './utils/helpers';
import storage from './storage';

const localStorage = storage.local;
const habitService = new HabitService();
const todoService = new ToDoService();

export interface AppServiceInt {
    checkAndDelete(day: Day): Promise<void | AppError>
}

export default class AppService implements AppServiceInt {
  async checkAndDelete(day: Day): Promise<void | AppError> {
    try {
      const result = localStorage.get(STORAGE_KEYS.todos) as ListObj;

      if (!result) {
        habitService.createDailyHabitTodos(day);
        return Promise.resolve();
      }

      const todayList = result[ListDayType.Today];
      const tomorrowList = result[ListDayType.Tomorrow];
      const habitList = result[ListDayType.DailyHabit];
      const hasTodayListExpired = Date.now() >= (todayList?.expireOn || 0);
      const tomorrowListStartDateValid = Date.now() >= (tomorrowList?.startDate || 0);
      const habitListExpired = Date.now() >= (habitList?.expireOn || 0);

      if (hasTodayListExpired) {
        await todoService.deleteTodoList(ListDayType.Today);
      }

      const carryOverList = this.getCarryOverList(todayList);

      if (tomorrowListStartDateValid) {
        await todoService.createList(ListDayType.Today, tomorrowList, todayList);
        await todoService.deleteTodoList(ListDayType.Tomorrow);
      } else if (carryOverList) {
        await todoService.createList(ListDayType.Today, carryOverList);
      }

      if (habitListExpired && habitList && habitList.todos.length > 0) {
        await habitService.addHabitCounts(habitList);
        await todoService.deleteTodoList(ListDayType.DailyHabit);
      }

      await habitService.removeExpiredHabits();
      habitService.createDailyHabitTodos(day);

      return Promise.resolve();
    } catch {
      return Promise.resolve(new AppError({
        type: 'general',
        code: ErrorCode.General,
      }));
    }
  }

  getCarryOverList(list?: ToDoList): ToDoList | null {
    if (!list?.todos.find((item) => item.carryOver && item.status !== 'done')) {
      return null;
    }

    return {
      save: false,
      todos: list?.todos.filter((item) => item.carryOver && item.status !== 'done'),
    };
  }

  async forceRefresh(day: Day): Promise<void | AppError> {
    try {
      const result = localStorage.get(STORAGE_KEYS.todos) as ListObj;

      if (!result) {
        habitService.createDailyHabitTodos(day);
        return Promise.resolve();
      }

      const todayList = result[ListDayType.Today];
      const tomorrowList = result[ListDayType.Tomorrow];
      const habitList = result[ListDayType.DailyHabit];

      await todoService.deleteTodoList(ListDayType.Today);

      const carryOverList = this.getCarryOverList(todayList);

      if (tomorrowList && tomorrowList.todos.length > 0) {
        await todoService.createList(ListDayType.Today, tomorrowList, todayList);
        await todoService.deleteTodoList(ListDayType.Tomorrow);
      } else if (carryOverList) {
        await todoService.createList(ListDayType.Today, carryOverList);
      }

      if (habitList && habitList.todos.length > 0) {
        await habitService.addHabitCounts(habitList);
        await todoService.deleteTodoList(ListDayType.DailyHabit);
      }

      return Promise.resolve();
    } catch {
      return Promise.reject(new AppError({
        type: 'general',
        code: ErrorCode.General,
      }));
    }
  }
}
