<template>
  <ul class="menu-list">
    <li
      class="menu-list__link"
      v-for="link in links"
      :key="link.key"
      :class="{'menu-list__link--active': link.link === activePath}"
      >
        <router-link :to="link.link">{{ t(`navLinks.${link.key}`) }}</router-link>
    </li>
    <li class="menu-list__link" v-if="isDevelop" @click="forceExpire">Force expiration</li>
  </ul>
</template>

<script lang="ts">
import { useRoute } from 'vue-router';
import { useI18n } from 'vue-i18n';
import { defineComponent, ref, watch } from 'vue';
import links from '@/services/utils/link';
import AppService from '@/services/appService';
import { AppError, dayMap } from '@/types/shared';

export default defineComponent({
  setup() {
    const activePath = ref('/');
    const route = useRoute();
    const { t } = useI18n();

    watch(route, () => {
      activePath.value = route.path;
    });

    const forceExpire = async () => {
      const result = await new AppService().forceRefresh(dayMap[(new Date()).getDay()]);
      if (result instanceof AppError) {
        console.log(result);
      } else {
        window.location.reload();
      }
    };

    return {
      activePath,
      links,
      t,
      isDevelop: process.env.NODE_ENV === 'development',
      forceExpire,
    };
  },
});
</script>

<style lang="scss" scoped>
.menu-list {
  @apply px-4;

  &__link {
    @apply px-2 py-2 pr-1 text-gray-500 hover:text-gray-800 font-medium;

    &--active {
      @apply bg-pink-900 bg-opacity-10;
    }
  }
}
</style>
